
@media screen and (max-width: 992px) {
html, body {
    background-color: #e2e4e6;
    font-size: 3vw
}
.form-control {
    height: 100%;
    font-size: 3vw
}
#label {
    font-size: 3vw
}
}
@media screen and (min-width: 992px) {
html, body {
    background-color: #7D7DA5;
}
}

